/* list item */
.lists li label {
  position: relative;
}

/*advice on checkbox style from article by Aditya Bhandari*/
/* checkbox */
.lists li label::before{
  content: '';
  display: inline-block;
  height: 16px;
  width: 16px;
  border: 2px solid #ffffff; 
  position: absolute;
  top: 5px;
  left: 0px;
}

/* check marks */
.lists li label::after {
  content: '';
  display: inline-block;
  height: 7px;
  width: 10px;
  border-left: 2px solid #ffffff;
  border-bottom: 2px solid #ffffff;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  position: absolute;
  top:  7px;
  left: 4px;
}

/*when input type checkbox is checked, li background  */
.lists li input[type="checkbox"] {
    opacity: 0;
    position: absolute;
}

.lists li input[type="checkbox"]:focus ~ p,
.deleteItem:focus,
.deleteItem:hover {
    color: #ff450b;
}

/* hides checkmark by default*/
.lists li input[type="checkbox"] ~ .items label::after {
    content: none;
}

/*Unhide the checkmark on the checked state*/
.lists li input[type="checkbox"]:checked ~ .items label::after {
    content: "";
}

.lists li input[type="checkbox"]:checked ~ .items {
    background: rgba(68, 168, 24, .73);
}
